body {
    background-color: rgb(240, 240, 240);
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image {
    width: 370px;
    margin-top: 50px;
}

@media (max-widh: 770px) {
    .container {
        margin: 0 10% !important;
    }
    .image {
        width: 100%;
    }
}
